<template>
    <div :class="{'img-show-mask':isBigImg}" @click="bigImg">
      <img :src="src" :class="{'bigImg':isBigImg,'image':!isBigImg}">
    </div>
</template>

<script>
    export default {
        name: "ImageView",
        props:['src'],
        data() {
          return{
            isBigImg: false,
          }
        },
      methods: {
        bigImg(){
          this.isBigImg = !this.isBigImg;
        }
      }
    }
</script>

<style scoped>
  .img-show-mask {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 3000;
    background: rgba(0, 0, 0, .5);
  }

  .bigImg {
    width: 500px;
    height: 280px;
    position: absolute;
    z-index: 3100;
    top: 0;
    left: 0;
    margin: auto;
  }

  .image {
    width: 100%;
    display: block;
  }
</style>



