<template>
    <div>

      <el-row v-for="(o, index) in 1" :key="o" :offset="index > 0 ? 1 : 0">
        <el-col :span="10" >
          <el-card :body-style="{ padding: '8px' }" shadow="hover">
            <div class="post_header">
              <div class="post_div" >
                <img src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="avatar"/>
              </div >
              <div  class="post_div post_margin" >
                <div class="username">username</div>
                <div class="time">2019-12-21 12:00</div>
              </div>
            </div>

            <div class="clearable contents_margin">
              <div class="content_margin">content</div>
              <div>
                <el-row :gutter="3">
                  <el-col :span="6">
                    <!--<ImageView  src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="clearable"></ImageView>-->

                    <viewer :images="signImages">
                      <img id="image" src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="image">
                    </viewer>
                  </el-col>
                  <el-col :span="6">
                    <img   src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="image">
                  </el-col>
                  <el-col :span="6">
                    <img  src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="image">
                  </el-col>
                </el-row>
                <el-row :gutter="3" style="margin-top: 4px">
                  <el-col :span="6">
                    <img src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="image">
                  </el-col>
                  <el-col :span="6">
                    <img src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="image">
                  </el-col>
                  <el-col :span="6">
                    <img src="http://element-cn.eleme.io/static/hamburger.50e4091.png" class="image">
                  </el-col>
                </el-row>
              </div>
            </div>

          </el-card>
        </el-col>
      </el-row>
    </div>
</template>

<script src="">
  import ImageView from  '@/components/ImageView.vue';
    export default {
      name: "Content",
      components:{ImageView},
      data () {
        return {
          currentDate: new Date(),
          show: false,
          image:"http://element-cn.eleme.io/static/hamburger.50e4091.png",
          signImages:[]
        }
      },
      methods: {

      }
    }
</script>

<style scoped>
  @import './../assets/css/font.css';

  .post_header{
    height: 45px;
  }

  .post_div{
    float: left;
  }

  .post_margin{
    margin-left: 8px;
  }

  .contents_margin{
    margin-left: 53px;
  }

  .content_margin{
    margin-top: 10px;
    margin-bottom: 8px;
  }
  .avatar {
    height: 45px;
    width: 45px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    /*margin-top: 15px;*/
    display: block;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearable{
    clear: both
  }


</style>



