<template>
    <div>
      <p v-model="msg">{{msg}}</p>
      <el-button type="info" @click="login" >登陆</el-button>
      <Content></Content>
    </div>
</template>

<script>

  import Content from '@/components/Content.vue'

    export default {
      name: "Home",
      components: {Content},
      data () {
          return {
            msg: "",
          }
        },
      methods:{
          login (){
            this.$router.push({path:"/login"})
          }
      },
      created() {

            this.axios.post("/login").then(response => {
              this.msg = response.data
              console.log(response.data)
            })
          }
    }
</script>

<style scoped>

</style>



